.not-found {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.not-found__title {
  color: #fff;
  text-align: center;
  font-size: 140px;
  font-weight: 400;
  line-height: normal;
  margin: 0;
  padding-top: 246px;
}

@media screen and (max-width: 769px) {
  .not-found__title {
    padding-top: 408px;
  }
}

@media screen and (max-width: 321px) {
  .not-found__title {
    font-size: 80px;
    padding-top: 329px;
  }
}

.not-found__information {
  color: #fff;
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  margin: 0;
}

@media screen and (max-width: 321px) {
  .not-found__information {
    font-size: 12px;
    margin-top: 10px;
  }
}

.not-found__link {
  color: #4285f4;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
  text-decoration: none;
  margin-top: 184px;
}

@media screen and (max-width: 321px) {
  .not-found__link {
    font-size: 12px;
    margin-top: 284px;
  }
}
